        <van-form @submit="onSubmit">
            <van-field v-model="tel" type="tel" label="手机号" placeholder="手机号"/>
            <van-field
                v-model="password"
                type="password"
                name="password"
                colon
                label="密码"
                placeholder="密码"
                :rules="[{ required: true, message: '请填写密码' }]"
            />
            <van-field
                v-model="sms"
                center
                clearable
                label="短信验证码"
                placeholder="请输入短信验证码"
                >
                <template #button>
                    <van-button size="small" type="button" @click="return getyzm()">发送验证码</van-button>
                </template>
            </van-field>
            <div style="margin: 16px;">
               <van-button round block type="info" native-type="submit"  color="rgb(16, 173, 81)">保存</van-button>
            </div>
        </van-form>
        
