<script>
import { Icon,Button,Form,Field ,Notify  } from 'vant';
export default {
    name:'login',
    data() {
        return {
            tel: '',
            password: '',
            sms:'',
            zhengyam:''
        }
    },
    components:{
        [Icon.name]:Icon,
        [Button.name]:Button,
        [Form.name]:Form,
        [Field.name]:Field,
        [Notify.name]:Notify 
    },
    methods:{
        getyzm(){
            // console.log(this.phone.length);
            if(this.tel.length===11){
                this.$axios({
                    method:'post',
                    url:' https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/sendSms',
                    data:{
                            "mobile":this.tel,
                            "type":'login'
                        }    
                    }).then(res=>{
                        this.zhengyam=res.data.data
                        
                        if (res.data.code===0) {
                            
                            Notify({ type: 'success', duration: 6000,message: '验证码' + this.zhengyam })
                        }else{
                            Notify({ type: 'danger', duration: 3000,message:res.data.msg})
                        }
                        // console.log("222",res);
                })
            }else{
                Notify('请输入正确的手机号')
            }
        },
        onSubmit() {
            console.log('submit', this.tel,this.password);
            if (this.sms.trim().length===0) {
                
            }else{
                this.$axios({
                    method:'post',
                    url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/forget',
                    data:{
                        'username':this.tel,
                        'password':this.password,
                        'vercode':this.sms
                    }
                }).then((res)=>{
                    // console.log("66",res.data);
                    if(res.data.code===0){
                        Notify({ type: 'success', duration: 3000,message:res.data.msg})
                        setTimeout(()=>{
                            this.$router.push('/login')
                        },1000)
                        
                    }else{
                        Notify({ type: 'danger', duration: 3000,message:res.data.msg})
                    }
                })
            }
            


        },
        go(){
            this.$router.go(-1);
        }
    }
}
</script>

